<template>
  <div class="abnormal">
   <span class="icon failed">&#xe7e6;</span>
    <p class="mt20 c333 fs16 bold">系统异常</p>
    <p class="mt10 c666 fs14">数据读取错误，请尝试重新支付</p>
    <div class="pl12 pr12" :class="{none:isShowrepayButton}">
      <button class="mt30 w" tag="button" @click="toAbnormal">重新支付</button>
    </div>
     
  </div>
</template>

<script>
export default {
  name: 'Abnormal',
  data () {
    return {
      isShowrepayButton: false,
      url:''
    }
  },
  created(){
    var repayurl = '';
    if(localStorage.repayurl){
      repayurl = localStorage.repayurl.replace(/\s+/g,"")
    }
    
    if(!repayurl){
      this.isShowrepayButton = true; 
    }else{
      this.isShowrepayButton = false; 
      this.url = repayurl;
    }
    
  },
  methods:{
    toAbnormal() {
      window.location.href =  this.url;
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.abnormal{
  padding-top: 50px;
  text-align: center;
  .failed{
    color:#ff722d;
    font-size:58px;
  }
}
</style>



